<template>
  <v-app>
    <v-dialog v-model="isDialog" width="60%" persistent>
      <v-card>
        <v-card-title></v-card-title>

        <v-card-text>
          <v-data-table :headers="headers" :items="devices" dense elevation-6>
          </v-data-table>
        </v-card-text>

        <v-card-actions>
          <v-btn
            color="primary"
            class="ma-2 white--text"
            elevation="2"
            @click="callLoaderAPI()"
            :disabled="isRunning == true"
            ></v-btn
          >

          <v-btn
            color="primary"
            class="ma-2 white--text"
            elevation="2"
            @click="callLoaderSelected()"
            :disabled="isRunning == true"
            >()</v-btn
          >

          <v-btn
            elevation="2"
            @click="isDialog = false"
            :disabled="isRunning == true"
            ></v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>
  
<script>
import {
  useGetSchedule,
  useLoadData,
} from "@/api/ManagementScreenTop/MSDevice";

const HEADERS = [
  { text: "", value: "name", sortable: true, width: "40%" },
  { text: "ID", value: "id", sortable: true, width: "10%" },
  { text: "", value: "status", sortable: true, width: "50%" },
];

export default {
  props: { shared /** DialogController */: { required: true } },

  data() {
    return {
      headers: HEADERS,
      devices: [],
      isDialog: false,
      isRunning: false,
      count: 0,
    };
  },
  mounted() {
    this.shared.mount(this);
  },
  methods: {
    // ======================================================
    // 
    // ======================================================
    initialize: function (devices) {
      this.isDialog = true; // 
      this.setDisplay(devices); // 
    },
    // ======================================================
    // 
    // ======================================================
    setDisplay: function (devices) {
      this.isRunning = false; // 

      this.devices.length = 0;
      for (const device of devices) {
        var item = {
          id: device.id,
          name: device.name,
          status: "",
        };
        this.devices.unshift(item);
      }
    },
    // ======================================================
    // API
    // ======================================================
    callLoaderAPI: function () {
      this.isRunning = true;
      // * 
      useGetSchedule(null)
        .then((response) => {
          const { status, message, data } = response["data"];
          if (status != 0) {
            throw new Error(message);
          }
          this.doLoaderList(data);
        })
        .catch((error) => {
          //
          alert("");
          console.log(error);
          this.isRunning = false;
        });
    },
    // ======================================================
    // API
    // ======================================================
    callLoaderSelected: function () {
      this.isRunning = true;
      // * 
      useGetSchedule(null)
        .then((response) => {
          const { status, message, data } = response["data"];
          if (status != 0) {
            throw new Error(message);
          }
          let list = [];
          for (const device of data) {
            if ((device.dataStatus & 8) == 0) {
              list.push(device);
            }
          }
          this.doLoaderList(list);
        })
        .catch((error) => {
          //
          alert("");
          console.log(error);
          this.isRunning = false;
        });
    },
    // ======================================================
    // 
    // ======================================================
    doLoaderList: function (list) {
      this.count = list.length;
      for (const device of list) {
        this.callLoader(device.id);
      }
    },
    // ======================================================
    // API
    // ======================================================
    callLoader: function (deviceId) {
      // * 
      this.changeStatus(deviceId, "");
      // * API
      useLoadData(deviceId)
        .then((response) => {
          //
          const { status, message, data } = response["data"];
          if (status != 0) {
            throw new Error(message);
          }
          for (const item of data) {
            this.checkStatus(item.id, item.status);
          }
        })
        .catch((error) => {
          console.log(error);
          alert("ID" + deviceId + "");
        })
        .finally(() => {
          this.count--;
          if (0 == this.count) {
            alert("");
            this.isRunning = false;
          }
        });
    },
    // ======================================================
    // 
    // ======================================================
    checkStatus(id, status) {
      if (status == -1) {
        this.changeStatus(id, "");
      } else if ((status & 1) > 0) {
        this.changeStatus(id, "");
      } else if ((status & 8) > 0) {
        this.changeStatus(id, "");
      } else {
        this.changeStatus(
          id,
          ""
        );
      }
    },
    // ======================================================
    // 
    // ======================================================
    changeStatus(targetId, message) {
      for (const item of this.devices) {
        if (item.id == targetId) {
          item.status = message;
          break;
        }
      }
    },
  },
};
</script>}
<style lang="scss">
</style>
  